body {
    margin: 0;
    padding: 0;
    font-family: verdana;
}

.center {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.ring {
    width: 300px;
    height: 300px;
    border-radius: 50%;
    box-shadow: 0 4px 0 #fcfafa;
    background: transparent;
    animation: animate 1s linear infinite;
}

.text {
    width: 300px;
    height: 300px;
    border-radius: 50%;
    color: #0e0b0b;
    position: absolute;
    top: 0;
    left: 0;
    text-align: center;
    line-height: 300px;
    font-size: 2em;
    background: transparent;
    box-shadow: 0 0 5px rgba(5, 5, 5, 0.979)
}

@keyframes animate {
    0%
    {
        transform: rotate(0deg);
    }
    100%
    {
        transform: rotate(360deg);
    }
}
